import hex_sha1 from "sha1";
import wPicker from "@/components/w-picker/w-picker.vue";
import {
	ChangePageTitle
} from '../../title.js'
import wx from 'weixin-js-sdk'
export default {
	data() {
		return {
			accessToken: '64e9275246c6545a3b5d3ad060fe4c516f44d556',
			appsercert: 'MTExMTEx',
			imgList: [],
			title: '',
			sha: '',
			idList: [],
			starttime: '请设置',
			endtime: '请设置',
			startYear: '2000',
			tabIndex: '',
			pickVal: [],
			mould_name: '默认模板',
			moulde_id: 1
		}
	},
	components: {
		wPicker
	},
	methods: {
		submit() {
			if (!this.title) {
				uni.showToast({
					title: '项目名称不能为空',
					icon: 'none'
				})
			} else {
				// var lists = [999]
				// for(var i = 0;i<this.imgList.length;i++){
				// 	lists.push(this.imgList[i].id)
				// }
				// console.log(lists)
				var lists = this.idList.join()
				// console.log(lists)
				if (this.starttime > this.endtime) {
					uni.showToast({
						title: '开始时间不能大于截止时间',
						icon: 'none'
					})
				} else {
					if (this.idList.length != 0) {
						var signStr =
							"access-token=" +
							this.accessToken +
							"&appsercert=" +
							this.appsercert +
							'&teamname=' +
							this.title +
							"&team_user=" +
							lists
						// console.log(signStr)
						this.sha = hex_sha1(signStr);
						console.log(this.sha)
						this.$axios({
								url: this.$baseUrl.addtask.url + '?access-token=' + this.accessToken + '&sign=' + this.sha,
								method: 'post'
							}, {
								'access-token': this.accessToken,
								sign: this.sha,
								teamname: this.title,
								team_user: lists,
								start_date: this.starttime,
								end_date: this.endtime,
								template_id: this.moulde_id
							})
							.then(res => {
								console.log(res)
								if (res.data.t_teamName == this.title) {
									uni.showToast({
										title: '添加成功'
									})
									this.title = ''
									this.imgList = []
									setTimeout(() => {
										uni.reLaunch({
											url: '/pages/item/index'
										})
									}, 1200)
								} else {
									uni.showToast({
										title: '添加失败',
										icon: 'none'
									})
								}

							}, (error) => {
								console.log(error)
							})
					} else {
						var signStr =
							"access-token=" +
							this.accessToken +
							"&appsercert=" +
							this.appsercert +
							'&teamname=' +
							this.title
						// console.log(signStr)
						this.sha = hex_sha1(signStr);
						console.log(this.sha)
						this.$axios({
								url: this.$baseUrl.addtask.url + '?access-token=' + this.accessToken + '&sign=' + this.sha,
								method: 'post'
							}, {
								'access-token': this.accessToken,
								sign: this.sha,
								teamname: this.title,
								start_date: this.starttime,
								end_date: this.endtime,
								template_id: this.moulde_id
							})
							.then(res => {
								if (res.data.t_teamName == this.title) {
									uni.showToast({
										title: '添加成功'
									})
									this.title = ''
									this.imgList = []
									setTimeout(() => {
										uni.reLaunch({
											url: '/pages/item/index'
										})
									}, 1200)
								} else {
									uni.showToast({
										title: '添加失败',
										icon: 'none'
									})
								}
							}, (error) => {
								console.log(error)
							})
					}
				}
			}

		},
		startChange(e) {
			this.starttime = e.detail.value
		},
		endChange(e) {
			this.endtime = e.detail.value
		},
		changeEndtime(index) {
			this.tabIndex = index;
			this.$refs.picker.show();
		},
		//截止时间
		onConfirm3(val) {
			console.log(val);
			this.endtime = val.result
		},
		changeStarttime(index) {
			this.tabIndex = index;
			this.$refs.picker2.show();
		},
		//开始时间
		onConfirm(val) {
			console.log(val);
			this.starttime = val.result
		},
		//项目模板
		gomouldlist() {
			uni.navigateTo({
				url: '/pages/mouldList/index'
			})
		},
		goAddMember() {
			var _this = this
			uni.request({
				url: 'http://testapi.shiliucrm.com/v1/hwechat_callback/callbacktask/getjsapiticket',
				data: {
					url: window.location.href.split('#')[0]
				},
				success: (res) => {
					console.log(res)
					wx.config({
						debug: false,
						beta: true,
						appId: res.data.appid,
						timestamp: res.data.timestamp,
						nonceStr: res.data.noncestr,
						signature: res.data.signature,
						jsApiList: [
							'selectEnterpriseContact'
						]
					});
					wx.ready(function() {
						var h = wx.invoke || WeixinJSBridge.invoke;
						h("selectEnterpriseContact", {
							fromDepartmentId: 1,
							mode: "multi",
							type: ["user"],
							selectedDepartmentIds: [],
							selectedUserIds: []
						}, function(res) {
							// alert(JSON.stringify(res))
							if (res.err_msg == "selectEnterpriseContact:ok") {
								if (typeof res.result == 'string') {
									res.result = JSON.parse(res.result) //由于目前各个终端尚未完全兼容，需要开发者额外判断result类型以保证在各个终端的兼容性
								}
								var selectedUserList = res.result.userList; // 已选的成员列表
								for (var i = 0; i < selectedUserList.length; i++) {
									var user = selectedUserList[i];
									_this.imgList = _this.imgList.concat(user.avatar)
									// alert(JSON.stringify(_this.imgList))
									var userId = user.id; // 已选的单个成员ID
									getID(userId)

									function getID(id) {
										uni.request({
											url: 'http://testapi.shiliucrm.com/v1/hwechat_callback/callbacktask/getuserid',
											data: {
												id: id
											},
											success: (res) => {
												// _this.idList = _this.idList.concat(res.data.user_id)
												this.idList.push(res.data.user_id)
											}
										})
									}
								}
							}
						})
					});
				},
			})
		}
	},
	onLoad() {
		// var list = uni.getStorageSync('login')
		// // alert(JSON.stringify(list))
		// if(list){
		// 	this.accessToken = list.access_token
		// 	this.appsercert = list.appsercert
		// }
		var list = uni.getStorageSync('login');
		if (list) {
			this.accessToken = list.access_token
			this.appsercert = list.appsercert
		}

	},
	onTabItemTap() {
		console.log('点击了添加项目页面')
		uni.switchTab({
			url: '/pages/additem/index'
		})
	},
	onShow() {




		let list = uni.getStorageSync('list')
		if (list) {
			console.log(list)
			this.moulde_id = list.moulde_id
			this.mould_name = list.moulde
			// this.imgList = this.imgList.concat(list) 
			uni.removeStorageSync('list')
		}
		var date = new Date()
		var y = date.getFullYear()
		var m = date.getMonth() + 1
		var d = date.getDate()
		console.log(y, m, d)
		// this.starttime = y + "-" + m + "-" + d;
		// this.endtime = y + "-" + m+1 + "-" + d; 
		this.pickVal = [y - 2000, m - 1, d - 1]
	}
}
